<template>
  <div class="post">
    <h3 class="me-title positionTopLeft"></h3>
    <div class="info-edit">
      <div class="info-edit-item">
        <div class="info-edit-item-label"></div>
        <div class="info-edit-item-content">
          <input
            type="text"
            v-model="model.title"
            placeholder=""
            class="me-input"
          >
        </div>
      </div>
      <div class="info-edit-item">
        <div class="info-edit-item-label"></div>
        <div class="info-edit-item-content">
          <g-select
            :selectList='articleList'
            chooseIndex='0'
            :nowdata="model.category"
            @g-selectValue="handleGetSelectValueCategory"
            id="ceatgory"
          ></g-select>
        </div>
      </div>
      <div class="info-edit-item">
        <div class="info-edit-item-label"></div>
        <div class="info-edit-item-content">
          <g-select
            :selectList='tagList'
            chooseIndex='1'
            :nowdata="model.tag"
            @g-selectValue="handleGetSelectValueTag"
            id="tips"
          ></g-select>
        </div>
      </div>
      <div class="info-edit-item">
        <div class="info-edit-item-label"></div>
        <div class="info-edit-item-content d-flex jc-start ai-center">
          <img
            :src="model.cover"
            v-if="model && model.cover"
            class="cover"
          >
          <g-upload @g-uploadList="handleGetUploadList"></g-upload>
        </div>
      </div>
      <div class="info-edit-item">
        <div class="info-edit-item-label"></div>
        <div class="info-edit-item-content">
          <VueEditor
            v-model="model.content"
            useCustomImageHandler
            @image-added="handleImageAdded"
          ></VueEditor>
        </div>
      </div>
      <div class="info-edit-item">
        <button
          class="me-button pointSB"
          @click="handleClickAddArticle"
        >{{postFlag ? '' : ''}}</button>
      </div>
    </div>
  </div>
</template>

<script>
import upload from '@/components/upload/index';
import select from '@/components/select/index';
import { VueEditor } from 'vue2-editor';
import mixin from '@/mixins';
export default {
  mixins: [mixin],
  components: {
    VueEditor,
    'g-upload': upload,
    'g-select': select
  },
  data() {
    return {
      articleList: ['', '', ''],
      tagList: ['js', 'css', 'html'],
      model: {
        title: '',
        category: '',
        tag: '',
        content: '',
        cover: '',
        userid: '',
        count: 1
      },
      postFlag: true // true   false 
    };
  },
  methods: {
    // upload
    handleGetUploadList(file) {
      var _self = this;
      // console.log(file);
      let formData = new FormData();
      formData.append('file', file);
      // 
      _self.$http.post('/upload', formData).then(res => {
        _self.$set(_self.model, 'cover', res.data.url);
        console.log(_self.model);
      });
    },
    // select   
    handleGetSelectValueCategory(value) {
      var _self = this;
      // console.log(value);
      _self.$set(_self.model, 'category', value[0]);
      console.log(_self.model);
    },
    // select   
    handleGetSelectValueTag(value) {
      var _self = this;
      console.log(value);
      var data = '';
      value.map((item, index) => {
        // console.log(item, index)
        //     
        data += index == 0 ? item : ',' + item;
      });
      _self.$set(_self.model, 'tag', data);
      // console.log(_self.model);
    },
    // 
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      var formData = new FormData();
      formData.append('file', file);
      const res = await this.$http.post('/upload', formData);
      // console.log(res);
      Editor.insertEmbed(cursorLocation, 'image', res.data.url);
      resetUploader();
    },
    // 
    handleClickAddArticle() {
      var _self = this;
      _self.$set(_self.model, 'userid', _self.tyqUser.userid);
      // 0
      let count = _self.model.count ? _self.model.count : 1;
      _self.$set(_self.model, 'count', count);
      console.log(_self.model);
      var allFlag = true;
      Object.keys(_self.model).forEach(key => {
        console.log(_self.model[key], key);
        // '' title
        // ''  category
        // 'js,css'  tag
        // 'ceshitest'  content
        // 'http://localhost:3000/upload/33.jpg'  cover
        // 1 'userid'
        if (!_self.model[key]) {
          //
          allFlag = false;
          console.log('');
        }
      });
      if (!allFlag) {
        _self.$gMessage({
          title: '!',
          duration: 2000,
          type: 'error'
        });
        return false;
      }
      // console.log(_self.model);
      if (_self.postFlag) {
        // postFlag true
        _self.$http.post('/addArticle', _self.model).then(res => {
          console.log(res);
          if (res.data.flag) {
            _self.$gMessage({
              title: '',
              duration: 2000,
              type: 'success'
            });
            _self.$router.push('/me/postlist');
          }
        });
      } else {
        // postFlag false
        console.log(_self.model);
        // return false;
        _self.$http.post('/editArticleById', _self.model).then(res => {
          console.log(res.data);
          if (res.data.flag) {
            _self.$gMessage({
              title: '',
              duration: 2000,
              type: 'success'
            });
            _self.$router.push('/me/postlist');
          }
        });
      }
    }
  },
  created() {},
  mounted() {
    var _self = this;
    // console.log(_self.$route.query.articleid);
    if (_self.$route.query.articleid) {
      _self.postFlag = false;
      _self.$http
        .get(`/getArticleByArticleId?articleid=${_self.$route.query.articleid}`)
        .then(res => {
          console.log(res.data);
          _self.model = res.data.res[0];
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.post {
  padding: 3.125rem 0 0 0;
  height: 100%;
  position: relative;
  overflow-y: scroll;
  /*  */
  -ms-overflow-style: none;
  scrollbar-width: none;
  // 
  &::-webkit-scrollbar {
    display: none;
  }
}
.cover {
  width: 5rem;
  height: 5rem;
}
</style>